<template>
    <div class="thnu_Loves container-fuild row">
         <!--  -->
        <div class="Loves_Broadcast col-lg-12">
            <div id="carousel-example-generic" class="carousel slide" data-ride="carousel">
            <!--  -->
                <ol class="carousel-indicators">
                    <li v-for="(item,index) in  loveslist" :key="index" data-target="#carousel-example-generic" :data-slide-to="index" :class="[ index == 0 ? 'active' : '']"></li>
                </ol>      
            <!--  -->
                <div class="carousel-inner" role="listbox">
                    <div v-for="(item,index) in  loveslist" :key="index" class="item" :class="[index == 0 ? 'active' : '']">
                        <a href="#" class="PC_img" ><img :src="item.url" alt=""></a>
                        <span class="Loves_date">{{item.date}}</span>
                        <div class="Loves_content">{{item.content}}</div>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="Loves_subs container-fuild row">
            <div class="lovesfree col-xs-12 col-sm-12 col-md-12 col-lg-12">
                 <li v-for="(item,index) in pageArr" :key="index">
                    <p class="freeshead">
                        <img :src="item.headurl" alt="">
                        <span>{{item.userName}}</span>
                        <span>{{item.date}}</span>
                    </p>
                    <v-gallery :images="item.photo" class="image-box">
                        <a href="#" :data-image="img" :title="item.content" v-for="(img,index) in item.photo" :key="index">
                            <div class="bgbox">
                                <img :src="img">
                            </div>
                        </a>
                         <div class="img-title" v-html="item.content"></div>
                    </v-gallery>
                </li>
            </div>
            <v-page :setting="pageSet" @page-change="pageChange"></v-page>
        </div>
        
    </div>
</template>
<script>
export default {
  data() {
    return {
      isMobile: "",
      pageSet: {
        info: true,
        totalRow: 0, //
        language: "cn", //default: 'cn'
        pageSizeMenu: [5, 10, 15, 50, 100] //default: [10, 20, 50, 100]
      },
      loveslist: [
        {
          content:
            "The most special feeling of human is the one-sided love. That's the unique. You would never see a cat loving another cat in secret. ",
          url: require("../assets/img/loves01.jpg"),
          date: "11-11"
        },
        {
          content:
            "",
          url: require("../assets/img/loves02.jpg"),
          date: "11-12"
        },
        {
          content:
            "Relationships dont need promises, terms &conditions. It just needs TWO people: who can trust& who can understand.",
          url: require("../assets/img/loves03.jpg"),
          date: "11-13"
        }
      ],
      list: [],
      pageArr: []
    };
  },
  created() {
    this.getWidth();
  },
  methods: {
    getWidth() {
      this.isMobile = $(window).width() > 768 ? true : false;
    },
    pageChange(pInfo) {
      let that = this;
      console.log(pInfo); //{pageNumber: 1, pageSize: 10}
      this.axios.get("/user/getLoveList").then(function(resp) {
        if (resp.data.status == "success") {
          that.list = resp.data.message;
          that.pageSet.totalRow = resp.data.message.length;
          // console.log(that.list);
          that.pageArr.splice(0, that.pageArr.length);
          let start = 0,
            end = 0;
          start = pInfo.pageSize * (pInfo.pageNumber - 1);
          end = start + pInfo.pageSize;
          if (end > that.list.length) end = that.list.length;
          for (let i = start; i < end; i++) {
            that.pageArr.push(that.list[i]);
          }
        }
      });
    }
  },
  mounted() {
    var startX, endX;
    // 
    var offset = 50;
    // 
    $("#carousel-example-generic")
      .on("touchstart", function(e) {
        // console.log("1111");
        // x
        startX = e.originalEvent.touches[0].clientX;
      })
      .on("touchmove", function(e) {
        //  move
        endX = e.originalEvent.touches[0].clientX;
      })
      .on("touchend", function(e) {
        // console.log(endX);
        //x endX
        //endXstartX
        var distance = Math.abs(startX - endX);
        if (distance > offset) {
          //
          // 
          $(this).carousel(startX > endX ? "next" : "prev");
        }
      });
  }
};
</script>
<style lang="less" scoped>
.thnu_Loves {
  width: 90%;
  margin: 0 auto;
  padding-top: 20px;
  /* background-color: pink; */
}
.Loves_Broadcast {
  padding-bottom: 25px;
}
.PC_img {
  display: block;
  width: 100%;
  /* background-color: pink; */
}
.PC_img img {
  display: block;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
li {
  list-style: none;
}
.carousel-indicators {
  position: absolute;
  bottom: 10px;
  left: 10%;
  z-index: 15;
  width: 60%;
  padding-left: 0;
  margin-left: -30%;
  text-align: center;
  list-style: none;
}
.carousel-indicators li {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 1px;
  text-indent: -999px;
  cursor: pointer;
  background-color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 10px;
}
.carousel-indicators .active {
  width: 12px;
  height: 12px;
  margin: 0;
  border-radius: 10px;
  background-color: #fff;
  box-sizing: border-box;
}
.Loves_content {
  width: 70%;
  height: 20%;
  position: absolute;
  left: 20%;
  bottom: 0%;
  background-color: #fff;
  border-radius: 10px;
  opacity: 0.6;
  z-index: 99999;
}
.Loves_date {
  position: absolute;
  right: 5%;
  bottom: 0%;
}
@media (max-width: 768px) {
  .PC_img {
    display: block;
    width: 100%;
    height: 200px;
    background-color: #ccc;
    opacity: 0.8;
  }
  .Loves_content {
    width: 70%;
    height: 50%;
    background-color: #fff;
    opacity: 0.5;
  }
}
.Loves_subs {
  margin: 0 auto;
  width: 100%;
  text-align: center;
}
.freeshead {
  display: inline-block;
  border-bottom: 1px solid rgb(51, 51, 51);
  margin: 0px 0px 10px;
}
.freeshead img {
  padding-left: 0;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
.freeshead span {
  color: rgb(51, 51, 51);
}
.image-box {
  .img-title {
    height: 60px;
    overflow: hidden;
    bottom: 5px;
    display: block;
    text-align: center;
    color: rgb(51, 51, 51);
    padding-top: 5px;
  }
  a {
    clear: both;
    display: inline-block;
    margin: 0 20px 20px 0;
    position: relative;
    text-align: center;

    .bgbox {
      background-color: #fff;
      height: 150px;
      display: table-cell;
      vertical-align: middle;
      padding: 4px;
      border-radius: 2px;
      img {
        width: 200px;
        height: 200px;
        display: block;
      }
    }

    &:hover {
      .img-title {
        color: #232323;
      }
    }
  }
}
.Loves_subs div.vPagination.vPaginationRight[data-v-5ffd519b] {
  text-align: center;
}
@media (max-width: 768px) {
  .lovesfree li {
    padding-top: 20px;
  }
  .image-box {
    a {
      clear: both;
      display: inline-block;
      margin: 0 20px 20px 0;
      position: relative;
      text-align: center;

      .bgbox {
        background-color: #fff;
        height: 80px;
        display: table-cell;
        vertical-align: middle;
        padding: 4px;
        border-radius: 2px;
        img {
          width: 70px;
          height: 70px;
          display: block;
        }
      }
    }
  }
}
</style>



