<template>
    <div class="thnu_Register container-fuild row">
        <h3 class="text-primary"></h3>
        <form class="addUser">
            <div class="form-group">
                <label for="userName"></label>
                <input type="text" class="form-control" id="userName" placeholder="!" v-model="registerForm.userName">
            </div>
            <div class="form-group">
                <label for="email"></label>
                <input type="email" class="form-control" id="email" placeholder="" v-model="registerForm.email">
            </div>
            <div class="form-group">
                <label for="password"></label>
                <input type="password" class="form-control" id="password" placeholder="" v-model="registerForm.password">
            </div>
            <div class="form-group">
                <label for="passwordOk"></label>
                <input type="password" class="form-control" id="passwordOk" placeholder="" v-model="registerForm.passwordOk">
            </div>
            <button type="button" class="btn btn-primary" @click="addUser" data-toggle="modal" data-target=".bs-example-modal-sm"></button>
        </form>

    </div>
</template>
<script>
import Footer from "./Footer.vue";
export default {
    data() {
        return {
            registerForm: {
                userName: null,
                password: null,
                passwordOk: null,
                email: null
            }
        };
    },
    mounted() {
        $("#userName").focus();
    },
    methods: {
        addUser: function() {
            if (this.registerForm.password != this.registerForm.passwordOk) {
                this.$toast.center("");
                return;
            } else if (
                !(
                    this.registerForm.password &&
                    this.registerForm.passwordOk &&
                    this.registerForm.userName &&
                    this.registerForm.email
                )
            ) {
                this.$toast.center("");
                return;
            }
            this.$loading("!!!");
            var userObj = {
                userName: this.registerForm.userName,
                password: this.registerForm.password,
                email: this.registerForm.email
            };
            userObj.password = this.crypto(userObj.password);
            this.axios
                .post("/admin/addUser", userObj)
                .then(response => {
                    if (response.data.status == "success") {
                        this.$loading.close();
                        this.$router.push("/");
                        this.$toast.center(",!!!");
                    } else {
                        this.$loading.close();
                        this.$toast.center(response.data.message);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        }
    }
};
</script>
<style lang="less" scoped>
h3 {
    padding-bottom: 10px;
    border-bottom: 1px solid #000;
    padding-left: 60px;
}
.addUser {
    width: 70%;
    margin: 20px auto;
}
.modal_title {
    border-bottom: 1px solid #ccc;
    padding: 5px;
    text-align: center;
}
#modal_content {
    text-align: center;
}
</style>



